import React from "react";
import DateFnsUtils from '@date-io/date-fns';
import { MuiPickersUtilsProvider, DateTimePicker } from "@material-ui/pickers";

function DateAndTime({ handleDateAndTimeInputChange, formValues }) {

  console.log('selectedDateAndTime: ', formValues.date)
  
  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <DateTimePicker
        disableToolbar
        variant="inline"
        margin="normal"
        name='date'
        autoOk
        ampm={false}
        disableFuture
        value={formValues.date}
        onChange={handleDateAndTimeInputChange}
        label="24h clock"
        format="dd/MM/yyyy HH:mm"
        minutesStep={5}
      />
  </MuiPickersUtilsProvider>
  );
}

export default DateAndTime;