import React from "react";
import { TextField } from '@material-ui/core'

const LessonFormStudent = ({ handleStudentsInputChange }) => {
    return (
        <TextField 
        id='name-input'
        label=' '
        required 
        fullWidth
        className='name'
        name='name'
        onChange={handleStudentsInputChange}
    />
    )
}

export default LessonFormStudent