import React from "react";
import { FormControl, RadioGroup, FormControlLabel, Radio } from '@material-ui/core'

const LessonFormClass = ({ handleStudentsInputChange }) => {
    return (
        <FormControl>
            <RadioGroup
                defaultValue='1'
                name='class'
                row
                onChange={handleStudentsInputChange}
            >
                <FormControlLabel value='1' control={<Radio style= {{color:'#8bc34a'}} /> } label='1' />
                <FormControlLabel value='2' control={<Radio style= {{color:'#8bc34a'}} /> } label='2' />
                <FormControlLabel value='3' control={<Radio style= {{color:'#8bc34a'}} /> } label='3' />
                <FormControlLabel value='4' control={<Radio style= {{color:'#8bc34a'}} /> } label='4' />
                <FormControlLabel value='5' control={<Radio style= {{color:'#8bc34a'}} /> } label='5' />
            </RadioGroup>
        </FormControl>
    )
}

export default LessonFormClass